import React from 'react';
import styles from './SpacificCategory.module.css';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';
import { ThreeDots } from 'react-loader-spinner';

export default function SpacificCategory() {
  let params = useParams();
  function getProductDetails(id){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}`)
  
    }
  let {isError , isLoading , data} = useQuery('categoryDetails',()=> getProductDetails(params.id));
  console.log(data)
  return <>
    <div className="container">
      <div className='row align-items-center'>
        {data?.data.data?<><div className='col-md-4'>
        <img className='w-100' src={data?.data.data.image} alt={data?.data.data.name} />
      </div>
      <div className='col-md-8'>
        <h2 className='text-center fw-bolder text-main'>{data?.data.data.name}</h2>
      </div></>:<button className='btn bg-white text-main mt-2' type='button'><ThreeDots
  visible={true}
  height="100"
  width="100"
  color="#4fa94d"
  radius="20"
  ariaLabel="three-dots-loading"
  wrapperStyle={{}}
  wrapperClass=""
  />
</button>}
      </div>
    </div>
  </>
}
