import React, { useState } from 'react';
import styles from './Register.module.css';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import {ThreeCircles} from 'react-loader-spinner';
import { Link } from 'react-router-dom';


export default function Register() {
  let navigate= useNavigate();
  const [error,seterror] =useState(null)
  const [isLoading,setisLoading]= useState(false)


  async function registerSubmit(values){
    setisLoading(true);
    let {data} = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`,values).catch((err) => 
    {
      setisLoading(false)
      seterror(err.response.data.message)
    })
    if (data.message === 'success'){
      setisLoading(false)
      navigate('/login')
    }
  }

  let phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

  let validationSchema = Yup.object({
    name:Yup.string().min(3,'name minlenght is 3').max(10,'name maxlenght is 10').required('name is required'),
    email:Yup.string().email('email is invalid').required('email is required'),
    phone:Yup.string().matches(phoneRegExp,'phone is invalid').required('phone is required'),
    password:Yup.string().matches(/^[A-Z][a-z0-9]{5,10}$/ , 'password start with uppercase').required('password is required'),
    rePassword:Yup.string().oneOf([Yup.ref('password')],'password and repassword does not match').required('repassword is reqired')
  })

  let formik = useFormik({
    initialValues:{
      name:'',
      email:'',
      password:'',
      rePassword:'',
      phone:''
    },validationSchema,
    onSubmit:registerSubmit
  })




  return <>
    <div className='w-75 mx-auto py-4'>
      {error?<div className='alert alert-danger'>{error}</div>:''}
      <h2>Resgister Now</h2>

      <form onSubmit={formik.handleSubmit}>
        <label htmlFor="name">Name:</label>
        <input type="text" name="name" id="name" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.name}  className='form-control mb-2'/>
        {formik.errors.name && formik.touched.name?<div className='alert mt-2 p-2 alert-danger'>{formik.errors.name}</div>:''}


        <label htmlFor="email">Email:</label>
        <input type="email" name="email" id="email" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email}  className='form-control mb-2'/>
        {formik.errors.email && formik.touched.email?<div className='alert mt-2 p-2 alert-danger'>{formik.errors.email}</div>:''}


        <label htmlFor="password">Password:</label>
        <input type="password" name="password" id="password" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.password}  className='form-control mb-2'/>
        {formik.errors.password && formik.touched.password?<div className='alert mt-2 p-2 alert-danger'>{formik.errors.password}</div>:''}


        <label htmlFor="rePassword">rePassword:</label>
        <input type="password" name="rePassword" id="rePassword" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.rePassword}  className='form-control mb-2'/>
        {formik.errors.rePassword && formik.touched.rePassword?<div className='alert mt-2 p-2 alert-danger'>{formik.errors.rePassword}</div>:''}


        <label htmlFor="phone">Phone:</label>
        <input type="tel" name="phone" id="phone" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.phone}  className='form-control mb-2'/>
        {formik.errors.phone && formik.touched.phone?<div className='alert mt-2 p-2 alert-danger'>{formik.errors.phone}</div>:''}


        {isLoading?<button className='btn bg-white text-main mt-2' type='button'><ThreeCircles
  visible={true}
  height="50"
  width="50"
  color="#4fa94d"
  ariaLabel="three-circles-loading"
  wrapperStyle={{}}
  wrapperClass=""
  />
</button>:<button disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white mt-2' type='submit'>Register</button>}
        
<div className='d-block mt-2'>
<Link to={'/Login'}> Login </Link>

</div>   
      </form>
    </div>
  </>
}
