import React, { useContext, useEffect, useState } from 'react';
import styles from './ProductDetails.module.css';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';
import { CartContext } from '../Context/cartContext';
import {Helmet} from "react-helmet";
import toast from 'react-hot-toast';


export default function ProductDetails() {
  let {addToCart} = useContext(CartContext);
  let params = useParams();
  let {isError , isLoading , data} = useQuery('productDetails',()=> getProductDetails(params.id));

  async function addProduct(productId){
    let response = await addToCart(productId);
    if(response.data.status === 'success'){
      toast.success('Product successfully added',{duration: 5000})
    }else{
      toast.error('Error in adding this product')
    }
  }
  function getProductDetails(id){
  return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`)

  }
// console.log(data?.data.data)



  return <>
    {data?.data.data?<div className='container'>
    <div className="application">
            <Helmet>
                <meta charSet="utf-8"/>
                <title>{data?.data.data.title}</title>
            </Helmet>
        </div>
    <div className='row py-2 align-items-center'>
      <div className='col-md-4'>
        <img className='w-100' src={data?.data.data.imageCover} alt={data?.data.data.title} />
      </div>
      <div className='col-md-8'>
        <h2 className='h5'>{data?.data.data.title}</h2>
        <p>{data?.data.data.description}</p>
        <h6 className='text-main'>{data?.data.data.category?.name}</h6>
        <h6 className='text-main'>Price : {data?.data.data.price} EGP</h6>
        <div className='d-flex justify-content-between'>
          <span>ratingsQuantity : {data?.data.data.ratingsQuantity}</span>
          <span><i className='fas fa-star rating-color'></i>{data?.data.data.ratingsAverage}</span>
        </div>
        <button onClick={()=>{addProduct(data?.data.data._id)}} className='btn bg-main text-white w-100 mt-3'>Add To Cart</button>
      </div>
    </div> 
    </div>:''}
  </>
}
