import './App.css';
import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom';
import Home from './Components/Home/Home'
import Products from './Components/Products/Products'
import Cart from './Components/Cart/Cart'
import Brands from './Components/Brands/Brands'
import Login from './Components/Login/Login'
import Register from './Components/Register/Register'
import Categories from './Components/Categories/Categories'
import Layout from './Components/Layout/Layout'
import Notfound from './Components/Notfound/Notfound'
import Profile from './Components/Profile/Profile'
import Address from './Components/Address/Address';
import Orders from './Components/Orders/Orders'
import SpacificBrand from './Components/SpacificBrand/SpacificBrand'
import SpacificCategory from './Components/SpacificCategory/SpacificCategory'
import UserContextProvider from './Components/Context/userContext';
import ProtectedRoute from './Components/ProtectedRoute/ProtectedRoute';
import ProductDetails from './Components/ProductDetails/ProductDetails';
import CartContextProvider from './Components/Context/cartContext';
import { Toaster } from 'react-hot-toast';

let routes = createHashRouter([
  { path: '/', element: <Layout />, children: [
    {index:true , element: <ProtectedRoute><Products/></ProtectedRoute>},
    {path:'Cart' , element:<ProtectedRoute><Cart/></ProtectedRoute>},
    {path:'Categories' , element:<ProtectedRoute><Categories/></ProtectedRoute>},
    {path:'Brands' , element:<ProtectedRoute><Brands/></ProtectedRoute>},
    {path:'Profile' , element:<ProtectedRoute>< Profile/></ProtectedRoute>},
    {path:'Address' , element:<ProtectedRoute><Address/></ProtectedRoute>},
    {path:'allorders' , element:<ProtectedRoute><Orders/></ProtectedRoute>},
    {path:'SpacificCategory/:id' , element:<ProtectedRoute><SpacificCategory/></ProtectedRoute>},
    {path:'SpacificBrand/:id' , element:<ProtectedRoute><SpacificBrand/></ProtectedRoute>},
    {path:'ProductDetails/:id' , element:<ProtectedRoute><ProductDetails/></ProtectedRoute>},
    {path:'Login' , element:<Login/>},
    {path:'Register' , element:<Register/>},
    {path:'*',element:<Notfound/>}
  ] }
])

function App() {

  return <CartContextProvider>
    <UserContextProvider>
    
    <RouterProvider router={routes}></RouterProvider>

    </UserContextProvider>
    <Toaster/>
  </CartContextProvider>
}

export default App;
