import React from 'react';
import styles from './SpacificBrand.module.css';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';
import { ThreeDots } from 'react-loader-spinner';


export default function SpacificBrand() {
  let params = useParams();
  function getBrandDetails(id){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/brands/${id}`)
  
    }
  let {isError , isLoading , data} = useQuery('brandDetails',()=> getBrandDetails(params.id));
  return <>
    <div className="container">
      <div className='row justify-content-center'>
        {data?.data.data?<><div className='col-md-9'>
        <img className='w-50 mx-auto' src={data?.data.data.image} alt={data?.data.data.name} />
      </div>
      </>:<button className='btn bg-white text-main mt-2' type='button'><ThreeDots
  visible={true}
  height="100"
  width="100"
  color="#4fa94d"
  radius="20"
  ariaLabel="three-dots-loading"
  wrapperStyle={{}}
  wrapperClass=""
  />
</button>}
      </div>
    </div>
  </>
}
