import React from 'react';
import styles from './Brands.module.css';
import { useQuery } from 'react-query';
import axios from 'axios';
import { Circles } from 'react-loader-spinner';
import { Link } from 'react-router-dom';


export default function Brands() {
  function getBrands(){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/brands`)
  }
  let {isLoading,data} = useQuery('AllBrands',getBrands);
  return <>
    <div className="container">
      <div className="row mt-4">
      
        {isLoading == false? <>{data?.data.data.map((brand,_id)=>{return<div  key={_id} className="col-md-3">
          <Link to={`/SpacificBrand/${brand._id}`}>
          <div className="categories">
            <img className='w-100' src={brand.image} alt={brand.name}/>
          </div>
          </Link>
          </div>

        })}</>:<>
        <div className="vh-100 d-flex justify-content-center align-items-center">
        <Circles
      height="80"
      width="80"
      color="#4fa94d"
      ariaLabel="circles-loading"
      wrapperStyle={{}}
      wrapperClass=""
      visible={true}
      />
        </div>
        </> }
        </div>
    </div>
  </>
}

