import React from 'react';
import styles from './Categories.module.css';
import axios from 'axios';
import { useQuery } from 'react-query';
import {Circles} from 'react-loader-spinner';
import { Link } from 'react-router-dom';

export default function Categories() {
  function getCategories(){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
  }
  let {isLoading,data} = useQuery('AllCategories',getCategories);
  return <>
    <div className="container">
      <div className="row mt-4">
      
        {isLoading == false? <>{data?.data.data.map((category,_id)=>{return<div  key={_id} className="col-md-2">
          <Link to={`/SpacificCategory/${category._id}`}>
          <div className="categories">
            <img className='w-100' src={category.image} alt={category.name}/>
            <h4 className='h6'>{category.name}</h4>
          </div>
          </Link>
          </div>

        })}</>:<>
        <div className="vh-100 d-flex justify-content-center align-items-center">
        <Circles
      height="80"
      width="80"
      color="#4fa94d"
      ariaLabel="circles-loading"
      wrapperStyle={{}}
      wrapperClass=""
      visible={true}
      />
        </div>
        </> }
        </div>
   
    </div>
  </>
}
