import axios from "axios";
import { error } from "jquery";
import { createContext, useEffect, useState } from "react";

export let CartContext = createContext();


let headers = {
    token:localStorage.getItem('userToken')
}

function addToCart(productId){
    return axios.post(`https://ecommerce.routemisr.com/api/v1/cart`,{productId},{
        headers:headers
    }).then((response)=>response).catch((error)=>error)
    }
    
    
    function getLoggedUserCart(){
        return axios.get('https://ecommerce.routemisr.com/api/v1/cart',{headers:headers}).then((response)=>response).catch((error)=>error)
    }
    
    function removeCartItem(productId){
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,{headers:headers}).then((response)=>response).catch((error)=>error)
    }
    
    function updateCartQuantity(productId,count){
        return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,{count},{headers:headers}).then((response)=>response).catch((error)=>error)
    }
    
    function onlinePayment(cartID,url,values){
        return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartID}?url=${url}`,{
            shippingAddress:values
        },{
            headers:headers
        }).then((response)=>response).catch((error)=>error)
    }
    
    export default function CartContextProvider(props){
        const [cartId , setCartId] = useState(null);
        async function getCart(){
            let {data} = await getLoggedUserCart();
            setCartId(data?.data._id)
        }
        useEffect(()=>{
            getCart();
        },[])
        
    
    
    return <CartContext.Provider value={{cartId ,addToCart , getLoggedUserCart , removeCartItem , updateCartQuantity , onlinePayment}}>
        {props.children}
    </CartContext.Provider>
}