import React, { useContext, useState } from 'react';
import styles from './Login.module.css';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import {ThreeDots} from 'react-loader-spinner';
import { Link } from 'react-router-dom';
import { UserContext } from '../Context/userContext';


export default function Login() {
  let {setUserToken} =useContext(UserContext);
  let navigate= useNavigate();
  const [error,seterror] =useState(null)
  const [isLoading,setisLoading]= useState(false)


  async function loginSubmit(values){
    setisLoading(true);
    let {data} = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signin`,values).catch((err) => 
    {
      setisLoading(false)
      seterror(err.response.data.message)
    })
    if (data.message === 'success'){
      setisLoading(false)
      localStorage.setItem(`userToken`,data.token)
      setUserToken(data.token)
      navigate('/')
    }
  }

  let validationSchema = Yup.object({
    email:Yup.string().email('email is invalid').required('email is required'),
    password:Yup.string().matches(/^[A-Z][a-z0-9]{5,10}$/ , 'password start with uppercase').required('password is required')
  })

  let formik = useFormik({
    initialValues:{
      email:'',
      password:''
    },validationSchema,
    onSubmit:loginSubmit
  })




  return <>
    <div className='w-75 mx-auto py-4'>
      {error?<div className='alert alert-danger'>{error}</div>:''}
      <h2>Login Now</h2>

      <form onSubmit={formik.handleSubmit}>

        <label htmlFor="email">Email:</label>
        <input type="email" name="email" id="email" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email}  className='form-control mb-2'/>
        {formik.errors.email && formik.touched.email?<div className='alert mt-2 p-2 alert-danger'>{formik.errors.email}</div>:''}


        <label htmlFor="password">Password:</label>
        <input type="password" name="password" id="password" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.password}  className='form-control mb-2'/>
        {formik.errors.password && formik.touched.password?<div className='alert mt-2 p-2 alert-danger'>{formik.errors.password}</div>:''}

        {isLoading?<button className='btn bg-white text-main mt-2' type='button'><ThreeDots
  visible={true}
  height="50"
  width="50"
  color="#4fa94d"
  radius="9"
  ariaLabel="three-dots-loading"
  wrapperStyle={{}}
  wrapperClass=""
  />
</button>:<button disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white mt-2' type='submit'>Login</button>}

<div className='d-block mt-2'>
<Link to={'/Register'}> Register </Link>

</div>
        
        
      </form>
    </div>
  </>
}
