import React, { useEffect , useState } from 'react';
import styles from './Products.module.css';
import axios from 'axios';
import {Circles} from 'react-loader-spinner';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import CategorySlider from '../CategorySlider/CategorySlider';
import MainSlider from '../MainSlider/MainSlider';


export default function Products() {


  function getProducts(){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products`)
  }


  let {isLoading,data} = useQuery('AllProducts',getProducts);

  // const [allProducts,setAllProducts] = useState(null);

  // async function getAllProducts(){
  //   const {data} = await axios.get('https://ecommerce.routemisr.com/api/v1/products')
  //   setAllProducts(data.data);
  // }


  // useEffect(()=>{
  //   getAllProducts()
  // },[])


  return <>

<MainSlider></MainSlider>
<CategorySlider></CategorySlider>

<div className="container py-5">
      <div className="row gy-4">

    {isLoading == false? <>
      {data?.data.data.map((product , _id)=>{
      return <div  key={_id} className="col-md-2">
        <Link to={`/ProductDetails/${product._id}`}>
          <div className="products">
            <img src={product.imageCover} className='w-100' alt={product.title}/>
            <h6 className='text-main fw-bolder'>{product.category.name}</h6>
            <h4 className="font-weight-bold">{product.title.split(' ').slice(0,2).join(' ')}</h4>
            <div className="d-flex justify-content-between align-items-center">
            <p>{product.price} EGP</p>
            <p><span><i className='fa-solid fa-star rating-color'></i></span> {product.ratingsAverage} </p>
            </div>
          </div>
        </Link>
        </div>
    })}
    </> : <>
    <div className="vh-100 d-flex justify-content-center align-items-center">
    <Circles
  height="80"
  width="80"
  color="#4fa94d"
  ariaLabel="circles-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
  />
    </div>
    </> }

    
    </div>
    </div>



    


    
  </>
}
