import React, { useContext, useEffect } from 'react';
import styles from './Cart.module.css';
import { CartContext } from '../Context/cartContext';
import { useState } from 'react';
import {Circles} from 'react-loader-spinner';
import { Link } from 'react-router-dom';

export default function Cart() {
  let {getLoggedUserCart, removeCartItem , updateCartQuantity} = useContext(CartContext);
  const [cartDetails,setcartDetails] = useState(null)
  async function getCart(){
    let {data} = await getLoggedUserCart();
    setcartDetails(data);
  }
  async function removeItem(id){
    let {data} = await removeCartItem(id);
    setcartDetails(data);
  }
  async function updateCount(id, count){
    let {data} = await updateCartQuantity(id,count);
    setcartDetails(data);
  }
  useEffect(()=>{
    getCart();
  },[])
  return <>
    {cartDetails?<div className='w-75 my-3 mx-auto p-3 bg-main-light'>
      <h3>Shopping Cart</h3>
      <h4 className='h6 text-main fw-bolder'> Cart Items : {cartDetails.numOfCartItems}</h4>
      <h4 className='h6 text-main fw-bolder mb-4'> Total Price : {cartDetails.data.totalCartPrice} EGP</h4>
      {cartDetails.data.products.map((product)=> <div className='row border-bottom py-2 px-2' key={product.product.id}>
        <div className="col-md-1">
          <img className='w-100' src={product.product.imageCover} alt={product.product.title} />
        </div>
        <div className="col-md-11">
          <div className='d-flex justify-content-between align-items-center'>
            <div>
              <h3 className='h6'>{product.product.title.split(' ').slice(0,3).join(' ')}</h3>
              <h6 className='text-main'>{product.price} EGP</h6>
            </div>
            <div>
              <button onClick={()=>updateCount(product.product.id , product.count +1)} className='btn brdr-main p-1'>+</button>
              <span className='mx-2'>{product.count}</span>
              <button onClick={()=>updateCount(product.product.id , product.count -1)} className='btn brdr-main p-1'>-</button>
            </div>
          </div>
          <button onClick={()=>removeItem(product.product.id)} className='btn p-0'><i className='text-danger fomt-sm fas fa-trash-can'></i> Remove</button>
        </div>
      </div>)}
      <Link to={'/Address'} className='btn m-2 bg-main w-25 text-white'>Online Payment</Link>
      <button className='btn m-2 bg-main w-25 text-white'>Cash on delivery</button>
    </div>:<div className="vh-100 d-flex justify-content-center align-items-center">
    <Circles
  height="80"
  width="80"
  color="#4fa94d"
  ariaLabel="circles-loading"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
  />
    </div>}
  </>
}
